@import url(https://fonts.googleapis.com/css?family=Roboto:400,300,100,100italic,300italic,400italic);
@import url(https://code.ionicframework.com/ionicons/2.0.1/css/ionicons.min.css);




body {


  /* text-align: center; */
  padding: 90px 0 0 0;
  box-sizing: border-box;
  font-family: 'Roboto';
  
}
.Dowanload
{
  padding: 140px 0 0 0;
}


.card {
  background: #fff;
  border-radius: 2px;
  display: block;
  min-height: 150px;
  margin: 1rem auto;
  position: relative;
  width: 900px;
  box-sizing: border-box;
  padding: 30px;
}



.card h2, .card h1 {
  display: block;
  color: #6580B2;
  margin: 0;
  padding: 0;
  font-size: 25px;
  font-weight: 400;
  text-align: left;
}

.card span.filename {
  display: block;
  text-align: left;
  color: #A3A3A3;
  margin: 10px 0;
}

.card h2.changelog_title {
  margin: 50px 0 0 0;
  font-size: 20px;
  padding: 0 0 10px 0;
  color: #555;
  border-bottom: 1px solid #CCC;
}

.card .changelog {
  width: 100%;
  height: auto;
  padding: 0;
  text-align: left;
  color: #555;
}

.card .changelog ul {
  padding: 10px 0 0 18px;
  margin: 0;
}

.card a.btn {
  height: 45px;
  padding: 0 25px;
  font-size: 18px;
  text-decoration: none;
  text-transform: uppercase;
  color: #ffffff;
  border-radius: 6px;
  background-color: #183153;
  border: none;
  margin: 25px 0 15px 0;
  display: inline-block;
  line-height: 45px;
  text-align: center;
 
}

